            <v-switch v-model="lightTheme" style="display: inline-block; vertical-align: middle" />
            <v-icon @click="lightTheme = true">mdi-weather-sunny</v-icon>
          </span>

          <h1>{{interpretedHost}}</h1>
        </div>
      </header>

      <router-view :backendPresent="backendPresent" />
    </v-content>
  </v-app>
