<script>
import axios from 'axios';

export default {
  name: 'App',

  data: () => ({
    lightTheme: false,
    activeComponents: [],

    // Not to be confused with the window.globals one, which is there only so as to hopefully populate a dropdown which only I can see anyway
    literatureCategories: ['Poezii', 'Eseuri']
  }),

  computed: {
    backendPresent () {
      return !this.activeComponents.includes('Full');
    },

    interpretedHost () {
      return window.globals.interpretedHost;
    },

    contactPageLink () {
      if (this.activeComponents.includes('Contact'))
        return '/contact';
      else
        return '/contact-placeholder';
    }
  },

  watch: {
    lightTheme: function(light) {
      this.$vuetify.theme.dark = !light;
    }
  },

  mounted () {
    axios({ method: 'GET', url: this.$apiUrl + '/activeComponents.json' })
      .then(result => {
          this.activeComponents = result.data;

          if (this.activeComponents.includes('L')) {
            axios({ method: 'GET', url: this.$apiUrl + '/l/categories' })
              .then(result => { window.globals.literatureCategories = this.literatureCategories = result.data.d; },
                    error  => { console.error(error); });
          }
        },
        error  => { console.error(error); });
    }
};
</script>
