import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue')
  },{
    path: '/a/:current?',
    props: true,
    name: 'Anything',
    component: () => import('../views/A.vue')
  },{
    path: '/projects',
    name: 'Projects',
    component: () => import('../views/Projects.vue')
  },{
    path: '/suhc',
    name: 'SUHC',
    component: () => import('../views/SUHC.vue'),
    children: [
      { path: 'log', name: 'Captain\'s log | SUHC', component: () => import('../views/SUHC-Log.vue') },
      { path: 'quotes', name: 'Quotes | SUHC', component: () => import('../views/SUHC-Quotes.vue') }
    ]
  },{
    path: '/l/admin',
    name: 'Administrare Literatură',
    component: () => import('../views/LiteratureAdmin.vue')
  },{
    path: '/l/:category?',
    name: 'Literature',
    props: true,
    component: () => import('../views/Literature.vue')
  },{
    path: '/contact',
    name: 'Contact',
    component: () => import('../views/Contact.vue')
  },{
    path: '*',
    name: '404',
    component: () => import('../views/404.vue')
  }
]

const beforeEach = (to, from, next) => {
  document.title = to.name + ' | ' + window.globals.interpretedHost;
  next();
}

const router = new VueRouter({
//  mode: 'history',
  routes
})
router.beforeEach(beforeEach);

export default router
